.zx-header {
	max-width: 100%;
	height: 100vh;
	background-image: url(../images/header-back.png);
	background-size: cover;
	position: relative;
	.zx-header-content {
		.menu {
			position: absolute;
			top: 80px;
			right: 180px;
			cursor: pointer;
			font-size: 22px;
		
		}
		.logo {
			margin: 20px 0 100px 0;
		}
		.zx-header-title-one {
			margin: 0 0 30px 0;
			font-family: s-bold;
			font-size: 48px;
		}
		.zx-header-title-two {
			margin: 0 0 0 0;
			font-family: reg;
			font-size: 22px;
			color: #000;
			&::after {
				content: "";
				display: block;
				width: 30px;
				height: 2px;
				background-color: #000;
				margin: 9px auto;
				-webkit-transition: .5s ease;
				transition: .5s ease;
			}
			&:hover {
				&::after {
					width: 60px;
				}
			}
		}
		.scroll-icon {
			width: 40px;
			height: 20px;
			position: absolute;
			bottom: 40px;
			left: 50%;
			transform: translateX(-50%);
			cursor: pointer;
			transition: all .3s ease-in-out;
			&:hover {
				bottom: 35px;
			}
		}
	}
}
.sec-two {
	padding: 100px 0;
	.why-us {
		.zx-sec-two-header {
			h4 {
				font-family: bold;
				font-size: 30px;
			}
			p {
				font-weight: light;
				font-size: 21px;
			}
		}
		.services {
			display: flex;
			justify-content: center;
			align-items: center;
			flex-direction: row-reverse;
			margin: 40px 0 0 0;
			.service {
				display: flex;
				justify-content: center;
				align-items: center;
				flex-direction: column;
				margin: 0 50px;
				&:hover {
					img {
