	}
	.owl-carousel .owl-item .img-conainer{
		height: 260px;
	}
	.owl-carousel .owl-item img {
		width: 100%;
		height: 100%;
	}
	.owl-dots {
		display: flex;
		align-items: center;
		justify-content: center;
		.owl-dot {
			background: #DDD !important;
			border-radius: 50%;
			color: inherit;
			border: none;
			padding: 0 !important;
			margin: 12px 3px;
			font: inherit;
			width: 8px;
			height: 8px;
		}
		.active {
